<template>
  <div id="FatherBox">
    <div class="header" :class="{ afterheader: this.$store.state.isTrue }">
      <div class="box1">
        <div class="title">
          <h1>铝片缺陷检测系统</h1>
        </div>
        <div class="imgs">
          <img class="img2" src="@/assets/intel.png" alt="" />
          <img class="img3" src="@/assets/haiyun.png" alt="" />
          <el-button
            type="primary"
            id="unLogin"
            @click="unLogin"
            icon="el-icon-circle-close"
            >退出</el-button
          >
        </div>
      </div>
    </div>

    <div class="secondBoox">
      <div id="leftBox">
        <div class="model">
          <p class="info_text">历史检测记录</p>
          <el-popover placement="right" width="250" v-model="visible">
            <p>各类型缺陷出现次数的柱状统计图</p>
            <div style="text-align: right; margin: 0">
              <el-button type="primary" @click="handleShow" class="barplot">{{
                show_button
              }}</el-button>
            </div>
            <el-button
              slot="reference"
              id="static"
              icon="el-icon-view"
              @click="handleShow2"
            ></el-button>
          </el-popover>
        </div>
        <div class="info">
          <history></history>
        </div>
      </div>

      <div id="rightBox" :class="{ afterRightBox: this.$store.state.isTrue }">
        <div class="send">
          <el-upload
            class="upload-demo"
            drag
            :action="baseURL + '/predict'"
            :on-success="handleSuccess"
            :show-file-list="false"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i class="el-icon-upload"></i>
            <div class="el-upload__text">
              将文件拖到此处，或<em>点击上传</em>
            </div>
          </el-upload>
          <div class="foot">原始图像</div>
        </div>
        <div class="result">
          <img v-if="predictImage" :src="predictImage" class="avatar" />
          <span class="wait" v-show="dataList.length === 0">等待上传</span>
          <div class="foot">检测结果</div>
        </div>
        <div class="resInfo">
          <ul>
            <li>class_name</li>
            <li>score</li>
            <li>xmin</li>
            <li>ymin</li>
            <li>xmax</li>
            <li>ymax</li>
          </ul>
          <ul v-for="data in dataList" :key="data">
            <li>{{ data['class_name'] }}</li>
            <li>{{ data['score'] }}</li>
            <li>{{ data['xmin'] }}</li>
            <li>{{ data['ymin'] }}</li>
            <li>{{ data['xmax'] }}</li>
            <li>{{ data['ymax'] }}</li>
          </ul>
          <span class="data" v-show="dataList.length === 0">暂无数据</span>
        </div>
      </div>
    </div>
    <plot v-if="this.ispicshow && visible" class="show"></plot>
  </div>
</template>
<script>
import history from '@/components/History.vue';
import plot from '@/components/Plot.vue';
import http from '@/utils/http.js';
export default {
  components: {
    history,
    plot,
  },
  data() {
    return {
      imageUrl: '',
      predictImage: '',
      dataList: [],
      visible: false,
      ispicshow: false,
      baseURL: '',
      show_button: '显示',
    };
  },
  mounted() {
    this.baseURL = http.defaults.baseURL;
  },
  methods: {
    handleSuccess(req, file, fileList) {
      this.imageUrl = URL.createObjectURL(file.raw);
      this.predictImage = `data:;base64,${req.image}`;
      this.dataList = req.result;
    },
    unLogin() {
      window.localStorage.clear();
      this.$router.push('/login');
    },
    handleShow() {
      this.ispicshow = !this.ispicshow;
      this.show_button = this.ispicshow ? '隐藏' : '显示';
      console.log(this.ispicshow);
    },
    handleShow2() {
      this.ispicshow = false;
      this.show_button = this.ispicshow ? '隐藏' : '显示';
    },
  },
};
</script>
<style scoped>
