import Vue from 'vue'
import VueRouter from 'vue-router'
import MyDetect from '@/views/My-Detect.vue'
import MyLogin from '@/views/My-Login.vue'
import MyWelcome from '@/views/My-Welcome.vue'

Vue.use(VueRouter) // 注册路由插件,和两个全局组件， router-view 和 router-link
// 配置表

const routes = [
  {
    path: '/detect',
    component: MyDetect,
    meta: {
      isLogin: true
    }
  },
  {
    path: '/login',
    component: MyLogin
  },
  {
    path: '/welcome',
    component: MyWelcome
  },
  {
    path: '*',
    redirect: '/welcome'
  }
]

const router = new VueRouter({
  // 加上history后，网页路由就没有#号了，好看！而且以后公司也会这样要求
  mode: 'hash',
  routes
})

// 全局拦截
router.beforeEach((to, from, next) => {
  console.log(to)

  // next 这个函数就相当于拦截后执行完上面的操作，执行到这个函数就跳转到to的组件，
  // 相当于循环中的break了
  if (to.meta.isLogin) {
    // 判断本地存储中是否有token
    if (localStorage.getItem('token')) {
      next() // 有token就进入center页面
    } else {
      next({
        path: '/login',
        // query会记住你之前想要进入的页面，当登录成功后，就直接跳转到该页面
        query: { redirect: to.fullPath }
      }) // 没有token进入登陆页面
    }
  } else {
    next()
  }
})
export default router
