<template>
  <div class="container" ref="test1">
    <!-- <div :class="[isShow ? 'sign-up-mode' : '', 'container']"> -->
    <div class="forms-container">
      <div class="signin-signup">
        <!-- <div class="test1"> -->
        <form action="" class="sign-in-form">
          <h2 class="title">登录</h2>
          <div class="input-field">
            <i class="fa fa-user"></i>
            <input type="text" placeholder="账 号" v-model="myuser" />
          </div>
          <div class="input-field">
            <i class="fa fa-lock"></i>
            <input type="password" placeholder="密 码" v-model="mypassword" />
          </div>
          <!-- <button type="submit" value="登录" class="btn solid"
                    @click="handleLogin">登录</button> -->
          <button class="btn solid" @click="handleLogin">登录</button>
        </form>
        <!-- </div> -->

        <form action="" class="sign-up-form">
          <h2 class="title">注册</h2>
          <div class="input-field">
            <i class="fa fa-user"></i>
            <input type="text" placeholder="用户名" v-model="registerName" />
          </div>
          <div class="input-field">
            <i class="fa fa-envelope"></i>
            <input type="text" placeholder="邮箱" />
          </div>
          <div class="input-field">
            <i class="fa fa-lock"></i>
            <input
              type="password"
              placeholder="密码"
              v-model="registerPassword"
            />
          </div>
          <button class="btn solid" @click="handleregister">确认</button>
        </form>
      </div>
    </div>

    <div class="panels-container">
      <div class="panel left-panel">
        <div class="content">
          <h3>点击下方按钮注册</h3>
          <button class="btn transparent" @click="handleSignUpBtn">注册</button>
        </div>

        <img
          src="@/assets/svg/undraw_to_the_stars_re_wq2x.svg"
          class="image"
          alt=""
        />
      </div>

      <div class="panel right-panel">
        <div class="content">
          <h3>点击下方按钮登录</h3>
          <button class="btn transparent" @click="handleSignInBtn">
            返回登录页面
          </button>
        </div>

        <img
          src="@/assets/svg/artificial_intelligence.svg"
          class="image"
          alt=""
        />
      </div>
    </div>
  </div>
</template>
<script>
import '@/assets/font-awesome-4.7.0/css/font-awesome.min.css';
import http from '@/utils/http.js';
import Router from '@/router/index.js';
export default {
  data() {
    return {
      myuser: '',
      mypassword: '',
      registerName: '',
      registerPassword: '',
      $axios: null,
    };
  },
  methods: {
    handleSignInBtn() {
      this.$refs.test1.classList.remove('sign-up-mode');
    },
    handleSignUpBtn() {
      this.$refs.test1.classList.add('sign-up-mode');
    },
    handleregister() {
      http
        .post('/register', {
          params: {
            username: this.registerName,
            password: this.registerPassword,
          },
        })
        .then(function (res) {
          console.log('后端返回注册信息:', res);
          if (res.data.state === 1) {
            window.alert('注册成功');
          } else {
            window.alert('注册失败，该用户名已存在');
          }
        });
    },
    handleLogin() {
      http
        .post('/login', {
          params: {
            username: this.myuser,
            password: this.mypassword,
          },
        })
        .then(function (res) {
          console.log('后端返回登录信息:', res);
          if (res.data.state === 1) {
            console.log('My-Login debug: 123: ', this);
            window.localStorage.setItem('token', 'login sucess');
            Router.push('/detect');
          } else if (res.data.state === -1) {
            window.alert('该账号未注册或者用户名不正确');
          } else {
            window.alert('密码错误');
          }
        });
    },
  },
  destroyed() {
    this.$store.commit('show');
  },
};
</script>
<style scoped>
/* @import url(../assets/css/login.css); */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body,
html {
  font-family: 'Poppins', sans-serif;
}
.container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  overflow: hidden;
}
.container:before {
  content: '';
  position: absolute;
  width: 2000px;
  height: 2000px;
  border-radius: 50%;
  background: linear-gradient(-45deg, #4481eb, #04befe);
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
  z-index: 6;
  transition: 2s ease-in-out;
}
.forms-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.signin-signup {
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
  width: 40%;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5;
  box-shadow: 0 2px 12px 0 rgb(0 0 0 / 10%);
  border-radius: 30px;
  transition: 1s 0.7s ease-in-out;
}
form {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  padding: 0.5rem;
  overflow: hidden;
  transition: 0.2s 0.7s ease-in-out;
}
form.sign-in-form {
  z-index: 2;
}
form.sign-up-form {
  z-index: 1;
  opacity: 0;
}
.title {
  font-size: 2rem;
  color: #444;
  margin-bottom: 10px;
}
.input-field {
  max-width: 380px;
  width: 100%;
  height: 55px;
  background-color: #f0f0f0;
  margin: 10px 0;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0 0.4rem;
}
.input-field i {
  text-align: center;
  line-height: 55px;
  color: #acacac;
  font-size: 1.1rem;
}
.input-field input {
  background: none;
  outline: none;
  border: none;
  line-height: 1; /*  根据元素字体大小自动设置行高 */
  font-weight: 600;
  font-size: 1.1rem;
  color: #333;
}
.input-field input::placeholder {
  color: #aaa;
  font-weight: 500;
}
.btn {
  width: 150px;
  height: 49px;
  border: none;
  outline: none;
  border-radius: 49px;
  cursor: pointer;
  background-color: #5995fd;
  color: #fff;
  text-transform: uppercase; /* 所有字母大写 */
  font-weight: 600;
  margin: 10px 0;
  transition: 0.5s;
}
.btn:hover {
  background-color: #4d84e2;
}
.social-text {
  padding: 0.7rem 0;
  font-size: 1rem;
}
.social-media {
  display: flex;
  justify-content: center;
}
.social-icon {
  height: 46px;
  width: 46px;
  border: 1px solid #333;
  margin: 0 0.45rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  text-decoration: none;
  color: #333;
  font-size: 1.1rem;
  transition: 0.3s;
}
.social-icon:hover {
  color: #4481eb;
  border-color: #4481eb;
}
.panels-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  text-align: center;
  z-index: 7;
}
.left-panel {
  padding: 3rem 17% 2rem 12%;
  pointer-events: all;
}
.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}
.panel .content {
  color: #fff;
  transition: 0.9s 0.6s ease-in-out;
}
.panel h3 {
  font-weight: 600;
  line-height: 1;
  font-size: 1.6rem;
  letter-spacing: 2px;
  margin-bottom: 20px;
}
.panel p {
  font-size: 0.95rem;
  padding: 0.7rem 0;
}
.btn.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}
.image {
  width: 100%;
  transition: 1.1s 0.4s ease-in-out;
}
.right-panel .content,
.right-panel .image {
  transform: translateX(800px);
}

.container.sign-up-mode:before {
  transform: translate(100%, -50%);
  right: 52%;
}

.container.sign-up-mode .left-panel .image,
.container.sign-up-mode .left-panel .content {
  transform: translateX(-800px);
}
.container.sign-up-mode .right-panel .image,
.container.sign-up-mode .right-panel .content {
  transform: translateX(0px);
}
.container.sign-up-mode .left-panel {
  pointer-events: none;
}
.container.sign-up-mode .right-panel {
  pointer-events: all;
}
.container.sign-up-mode .signin-signup {
  left: 25%;
}
.container.sign-up-mode form.sign-in-form {
  z-index: 1;
  opacity: 0;
}
.container.sign-up-mode form.sign-up-form {
  z-index: 2;
  opacity: 1;
}

@media (max-width: 870px) {
  .container {
    min-height: 800px;
    height: 100vh;
  }

  .container:before {
    width: 1500px;
    height: 1500px;
    left: 30%;
    bottom: 68%;
    transform: translateX(-50%);
    right: initial;
    top: initial;
    transition: 2s ease-in-out;
  }

  .signin-signup {
    width: 100%;
    left: 50%;
    top: 95%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .panels-container {
    z-index: 10;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
  }

  .panel .content {
    padding-right: 15%;
    transition: 0.9s 0.8s ease-in-out;
  }

  .panel h3 {
    font-size: 1.2rem;
  }

  .panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .btn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }
  .image {
    width: 200px;
    transition: 0.9s 0.6s ease-in-out;
  }

  .left-panel {
    grid-row: 1 / 2;
  }
  .right-panel {
    grid-row: 3 / 4;
  }
  .right-panel .content,
  .right-panel .image {
    transform: translateY(300px);
  }

  .container.sign-up-mode:before {
    transform: translate(-50%, 100%);
    bottom: 32%;
    right: initial;
  }

  .container.sign-up-mode .left-panel .image,
  .container.sign-up-mode .left-panel .content {
    transform: translateY(-300px);
  }

  .container.sign-up-mode .signin-signup {
    top: 5%;
    transform: translate(-50%, 0);
    left: 50%;
  }
}

@media (max-width: 570px) {
  form {
    padding: 0 1.5rem;
  }

  .image {
    display: none;
  }

  .panel .content {
    padding: 0.5rem 1rem;
  }

  .container:before {
    bottom: 72%;
    left: 50%;
  }
  .container.sign-up-mode:before {
    bottom: 28%;
    left: 50%;
  }
}
</style>
