<template>
  <div class="box">
    <ul @scroll="handleScroll" class="afterul">
      <li v-for="data in dataList" :key="data.id" @click="handleDetail(data)">
        <img :src="`data:;base64,${data.image}`" alt="" style="width: 100%" />
      </li>
    </ul>
    <div class="detail" v-if="item">
      <img :src="`data:;base64,${item.image}`" alt="" style="width: 100%" />
      <i class="el-icon-close" @click="handleBack"></i>
      <div>predict_time: {{ item.predict_time }} ms</div>
      <div>
        <ul class="detection_result">
          <li v-for="(value, title) in item.result[0]" :key="title">
            {{ title }}
          </li>
        </ul>
        <ul
          v-for="result in item.result"
          :key="result.name"
          class="detection_result"
        >
          <li v-for="title in result" :key="title">{{ title }}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import http from '@/utils/http.js';
export default {
  data() {
    return {
      dataList: [],
      pageNum: 1,
      pageSize: 10,
      item: '',
      ishave: true,
    };
  },
  mounted() {
    http
      .post('/history', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
        },
      })
      .then((res) => {
        console.log('history: ', res);
        this.dataList = res.data.history;
        this.pageNum++;
      });
  },
  methods: {
    handleDetail(data) {
      this.item = data;
      this.$store.state.isTrue = true;
      console.log('点击');
    },
    handleBack() {
      this.item = '';
      this.$store.state.isTrue = false;
    },
    handleScroll(event) {
      if (
        event.target.scrollHeight ===
        event.target.clientHeight + event.target.scrollTop
      ) {
        if (this.ishave) {
          http
            .post('/history', {
              params: {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
              },
            })
            .then((res) => {
              console.log('history: ', res);
              this.dataList = this.dataList.concat(res.data.history);
              this.pageNum++;
              if (res.data.history.length === 0) {
                this.ishave = false;
              }
            });
        }
      }
    },
  },
};
</script>

<style scoped>
html,
body {
  height: 100%;
}
.box {
  height: 100%;
}
ul {
  overflow-y: scroll;
  height: 100%;
}
.afterul {
  /* display: flex;
  flex-direction: column;
  width: 100%; */
}
li {
  cursor: pointer;
}
.detail {
  position: absolute;
  /* margin: -25% 35%; */
  right: 200px;
  top: 0px;
  width: 640px;
  height: 100%;
  z-index: 100;
  background: white;
}
i {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  color: red;
  font-size: 30px;
}
.detection_result {
  display: flex;
  flex-direction: row;
  list-style: none;
  overflow-y: hidden;
}
.detection_result > li {
  flex: 1;
}
</style>
