<template>
  <div id="plot"></div>
</template>

<script>
import * as echarts from 'echarts';
import http from '@/utils/http.js';
export default {
  data() {
    return {
      bar_data: {
        zhen_kong: 0,
        ca_shang: 0,
        zang_wu: 0,
        zhe_zhou: 0,
      },
    };
  },
  mounted() {
    const chartDom = document.getElementById('plot');
    const myChart = echarts.init(chartDom);
    http.get('/plot').then((res) => {
      res.data.history.forEach((item) => {
        item.result.forEach((result) => {
          this.bar_data[result.class_name]++;
        });
      });
      const xdata = Object.keys(this.bar_data);
      const ydata = Object.values(this.bar_data);
      console.log('plot xdata:', xdata);
      console.log('plot ydata:', ydata);
      const option = {
        title: {
          text: '历史缺陷检测数量',
          // top: 'center',
          right: 'center',
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow',
          },
        },
        xAxis: {
          type: 'category',
          data: xdata,
        },
        yAxis: {
          type: 'value',
        },
        series: [
          {
            data: ydata,
            type: 'bar',
          },
        ],
      };
      myChart.setOption(option);
    });
  },
};
</script>

<style scoped>
#plot {
  width: 500px;
  height: 500px;
  position: absolute;
  right: 70px;
  top: 100px;
  box-shadow: 0 2px 12px 0 rgb(0 0 0 / 15%);
  background-color: white;
}
</style>
